<template>
  <div class="login">
    <div class="closeWrapper">
      <!-- <van-icon name="cross" @click="closeWrapper" /> -->
    </div>
    <img class="logo" src="../../assets/szlogo.png" alt />
    <van-field class="info" v-model="username" name="" label="" placeholder="" />
    <van-field
      class="info"
      v-model="password"
      type="password"
      name=""
      label=""
      placeholder=""
    />
    <van-field class="info verify" v-model="verify" line-block placeholder="" />
    <div class="svgVcodeBox">
      <span @click="getVcode()" id="svgVcode"></span>
    </div>

    <slot></slot>
    <div style="margin: 16px;">
      <van-button
        id="btn"
        :disabled="ableToSubmit()"
        @click="onSubmit"
        round
        block
        type="info"
        native-type="submit"
      >{{buttonName}}</van-button>
    </div>
  </div>
</template>
<script>
import Vue from "vue";
import { Button, Field, Checkbox, Notify } from "vant";
Vue.use(Button);
Vue.use(Field);
Vue.use(Checkbox);
Vue.use(Notify);
export default {
  props: {
    //   getVcode:{type:Function},
    buttonName: { type: String },
    checked: { type: Boolean }
    // showReg:{type:Boolean},
    // showLogin:{type:Boolean}
  },
  data() {
    return {
      username: "",
      password: "",
      verify: ""
    };
  },
  created() {
    this.getVcode();
  },
  methods: {
    async getVcode() {
      const result = await fetch(`http://www.ihuanu.cn/api/vcode`, {
        credentials: "include"
      }).then(res => res.json());

      if (result.code === 1) {
        document.querySelector("#svgVcode").innerHTML = result.data;
      }
    },
    goto() {
      this.$router.push("/reg");
    },
    closeWrapper() {
      this.show = false;
    },
    ableToSubmit() {
      if (this.password && this.username && this.verify && this.checked) {
        return false;
      }
      return true;
    },

    async onSubmit() {
      if (this.buttonName === "") {
        let queryStr = "";
        queryStr += `password=${this.password}&username=${this.username}&vcode=${this.verify}&mdl=${this.checked}`;
        console.log(queryStr);
        fetch(`http://www.ihuanu.cn/api/login?${queryStr}`, {
          credentials: "include"
        })
          .then(res => res.json())
          .then(data => {
            if (data.code == 1) {
              localStorage.setItem(
                "userInfo",
                JSON.stringify(data.data.authorization)
              );
              console.log(data);
              localStorage.setItem("szbookUsername", this.username);
              localStorage.setItem(
                "szbookcarInfo",
                JSON.stringify(data.data.cartInfo)
              );

              Notify({ type: "success", message: "" });
              this.$router.replace("/home");
              this.$store.commit("addUserInfo");
            } else if (data.code == 10) {
              Notify({ type: "danger", message: "" });
              this.getVcode();
              this.verify = "";
            } else {
              Notify({ type: "danger", message: "" });
            }
          });
      }
      if (this.buttonName === "") {
        //

        fetch(`http://www.ihuanu.cn/api/reg/check?username=${this.username}`)
          .then(res => res.json())
          .then(res => {
            if (res.code == 0) {
              Notify({ type: "danger", message: "" });
            } else {
              const url = "http://www.ihuanu.cn/api/reg";
              fetch(url, {
                method: "post",
                credentials: "include",
                headers: {
                  "Content-Type": "application/json"
                },
                body: JSON.stringify({
                  username: this.username,
                  password: this.password,
                  vcode: this.verify,
                  cartInfo: []
                })
              })
                .then(res => res.json())
                .then(res => {
                  console.log(res);
                  if (res.code == 10) {
                    Notify({ type: "danger", message: "" });
                    this.getVcode();
                  } else {
                    Notify({ type: "success", message: "" });
                    //
                    this.$emit("goto");
                  }
                });
            }
          });
      }
    }
  }
};
</script>
